<template>
  <div class="news-container">
    <div class="news-header-container">
      <div class="news-header-title clear-fix">
        <span></span>
        <a href="javascript:">
          <img src="../assets/image/icon_more_tool.png"/>
        </a>
        <a href="javascript:">
          <img src="../assets/image/icon-myfriend.png"/>
        </a>
      </div>
      <div class="news-subtitle clear-fix">
        <span></span>
        <a href="javascript:">
          <img src="../assets/image/icon_clear_msg.png"/>
        </a>
      </div>
    </div>
    <div class="card-container">
      <ul class="clear-fix">
        <li><a href="javascript:">
          <img src="../assets/image/icon_msg_kinds_01.png"/>
          <span></span>
        </a></li>
        <li><a href="javascript:">
          <img src="../assets/image/icon_msg_kinds_02.png"/>
          <span></span>
        </a></li>
        <li><a href="javascript:">
          <img src="../assets/image/icon_msg_kinds_03.png"/>
          <span></span>
        </a></li>
      </ul>
    </div>
    <div class="msg-list-container">
      <ul>
        <li v-for="(item, index) in msgInfo" :key="index">
          <a href="javascript:" class="clear-fix">
            <div class="msg-icon">
              <img :src="item.url"/>
            </div>
            <div class="msg-content">
              <p class="line1"><span>{{item.title}}</span><span>{{item.time}}</span></p>
              <p class="line2">{{item.msg}}</p>
            </div>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "news",
    description: "",
    data() {
      return {
        msgInfo: [
          {url: '../static/img_msg_obj_01.png', title: '', msg: ':', time: '12:12'},
          {url: '../static/img_msg_obj_02.png', title: '', msg: '[]', time: ''},
          {url: '../static/img_msg_obj_01.png', title: '', msg: '[]', time: ''},
          {url: '../static/img_msg_obj_01.png', title: '', msg: '[]', time: ''},
          {url: '../static/img_msg_obj_01.png', title: '', msg: '[]', time: ''}
        ]
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .news-container {
    background: -webkit-linear-gradient(to right, #fcfcfc, #f1f1f1); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #fcfcfc, #f1f1f1); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #fcfcfc, #f1f1f1);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #fcfcfc, #f1f1f1);
    overflow-x: hidden;
    padding-bottom: 4rem;
  }

  .news-header-container {
    height: 13rem;
    color: white;
    background: -webkit-linear-gradient(to right, #ff8a00, #ff2000); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #ff8a00, #ff2000); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #ff8a00, #ff2000);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #ff8a00, #ff2000);
  }

  .news-header-title {
    width: 95%;
    margin: 0 auto;
    padding: 0.4rem 0;
  }

  .news-header-title span {
    float: left;
    font-size: 1.2rem;
  }

  .news-header-title a {
    float: right;
  }

  .news-header-title a img {
    height: 1.3rem;
    padding: 0.1rem 0;
  }

  .news-header-title a:last-child img {
    height: 1.5rem;
    margin-right: 2rem;
    padding: 0;
  }

  .news-subtitle {
    width: 95%;
    margin: 0 auto;
    padding: 0.4rem 0;
  }

  .news-subtitle span {
    float: left;
  }

  .news-subtitle img {
    height: 1.5rem;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 50%;
    float: left;
    margin-left: 1rem;
  }

  .card-container {
    background-color: white;
    width: 95%;
    margin: -7rem auto 0.9rem;
    border-radius: 1em;
    padding: 1rem 0 1.5rem;
    box-shadow: 0 0.3rem 0.1rem 0.3rem rgba(85, 85, 85, 0.1);
    position: relative;
    z-index: 1;
  }

  .card-container li {
    float: left;
    width: calc(100% / 3);
  }

  .card-container img {
    height: 3.4rem;
  }

  .card-container a {
    display: inline-block;
    color: #626563;
    font-size: 0.8rem;
  }

  .card-container span {
    display: block;
    padding-top: 0.4rem;
  }

  .msg-list-container {
    background-color: white;
    padding: 2rem 0;
    margin-top: -1rem;
  }

  .msg-icon {
    float: left;
    padding: 0.5rem;
  }

  .msg-icon img {
    width: 3.5rem;
  }

  .msg-content {
    float: left;
    color: #333;
    width: calc(100% - 5.5rem);
    height: 3.5rem;
    padding: 0.5rem 0.5rem 0.5rem 0;
    margin-left: 0.5rem;
    line-height: 1.5rem;
    border-bottom: 1px solid #efefef;
    text-align: left;
  }

  .msg-content .line1 {
  }

  .msg-content .line1 span:last-child {
    float: right;
    color: #9c9a9c;
    font-size: 0.8rem;
  }

  .msg-content .line2 {
    font-size: 0.8rem;
    color: #9c9a9c;
  }
</style>



