<template>
  <div class="product-card clear-fix">
    <div class="product">
      <a href="javascript:">
        <img :src="productImg">
        <p class="product-name">{{productName}}</p>
        <div class="product-info clear-fix">
          <span class="product-price"><small></small>{{productPrice}}</span>
          <span class="product-sale"><small>{{productSale}}</small></span>
          <span v-if="cardKind===1" onclick="return false;" class="more-info">
            <img src="../assets/image/more-info.png">
          </span>
          <span v-if="cardKind===2" onclick="return false;" class="sim-product"></span>
        </div>
      </a>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ProductCard",
    props: {
      productImg: {default: "../static/picture_product_00.png"},
      productName: {default: ""},
      productPrice: {default: "0.00"},
      productSale: {default: "0"},
      cardKind: {default: 1}
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .product-card {
    width: 100%;
    margin-top: 0.8rem;
    overflow: hidden;
    border-radius: 0.8rem;
    font-size: 0.9rem;
  }

  .product {
    float: left;
    width: 96%;
    margin-left: 2%;
    background-color: white;
    text-align: center;
    position: relative;
  }

  .product img:first-child {
    width: 100%;
  }

  .product-name {
    color: #333;
    text-align: left;
    width: 90%;
    height: 4rem;
    line-height: 2rem;
    margin: 0 auto;
  }

  .product-info {
    width: 90%;
    margin: 0 auto;
    padding-bottom: 1rem;
  }

  .product-price {
    float: left;
    color: #ff2d00;
  }

  .product-sale {
    float: left;
    color: #9a9a9a;
    margin-left: 0.5rem;
  }

  .more-info {
    display: inline-block;
    float: right;
    width: 12%;
  }

  .sim-product {
    color: #666666;
    font-size: 0.7rem;
    padding: 0.1rem 0.3rem;
    border: 1px solid #b7b7b7;
    border-radius: 1rem;
    float: right;
  }

</style>



