<template>
  <div class="tiny-tb-container">
    <div class="header-container">
      <div class="header-title clear-fix">
        <span></span>
        <a href="javascript:">
          <img src="../assets/image/icon-myfriend.png"/>
        </a>
        <a href="javascript:">
          <img src="../assets/image/icon-search.png"/>
        </a>
      </div>
      <div class="subtitle">
        <ul class="clear-fix">
          <li v-for="(item, index) in subtitleList" :key="index" :class="{'underline':focusFlag === index}"
              @click="changeFocus(index)">
            <span>{{item}}</span>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-container">
      <div class="card-container" v-for="(item, index) in ttbInfo" :key="index">
        <div class="card-header">
          <img class="seller-icon" src="../assets/image/user-icon.png"/>
          <img class="icon-shop" src="../assets/image/icon-shop.png"/>
          <p class="shop-name">{{item.shopName}}</p>
          <p class="release-time">{{item.time}}</p>
          <img class="delete-card" src="../assets/image/icon-delete-card.png">
        </div>
        <div class="card-content">
          <p class="display-text" v-html="item.text">{{item.text}}</p>
          <div class="display-img">
            <ul class="clear-fix">
              <li v-for="(subItem, subIndex) in item.url" :key="subIndex">
                <img :src="subItem"/>
              </li>
            </ul>
          </div>
        </div>
        <div class="card-footer clear-fix">
          <div class="visited">
            <span>{{item.visited}}</span>
          </div>
          <div class="follow">
            <img src="../assets/image/icon-follow.png"/>
            <span v-if="item.follow===0"></span>
            <span v-else>{{item.follow}}</span>
          </div>
          <div class="evaluate">
            <img src="../assets/image/icon-evaluate.png"/>
            <span v-if="item.evaluate===0"></span>
            <span v-else>{{item.evaluate}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "tiny-tb-page",
    description: "",
    data() {
      return {
        subtitleList: ['', '', '', '', '', '', '', 'sir', '', ''],
        focusFlag: 0,
        ttbInfo: [
          {
            shopName: '',
            time: '',
            text: '<br>',
            url: ['../static/img-tiny-tb-display_01.png', '../static/img-tiny-tb-display_02.png', '../static/img-tiny-tb-display_03.png', '../static/img-tiny-tb-display_04.png', '../static/img-tiny-tb-display_05.png', '../static/img-tiny-tb-display_06.png', '../static/img-tiny-tb-display_07.png', '../static/img-tiny-tb-display_08.png', '../static/img-tiny-tb-display_09.png'],
            visited: '1',
            evaluate: 0,
            follow: 1
          },
          {
            shopName: '',
            time: '10',
            text: '',
            url: ['../static/img-tiny-tb-display_01.png', '../static/img-tiny-tb-display_02.png', '../static/img-tiny-tb-display_03.png', '../static/img-tiny-tb-display_04.png', '../static/img-tiny-tb-display_05.png'],
            visited: '1.2',
            evaluate: 17,
            follow: 987
          }
        ],
      }
    },
    methods: {
      changeFocus(index) {
        this.focusFlag = index;
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .tiny-tb-container {
    background-color: #f8f2f7;
    overflow-x: hidden;
    padding-bottom: 5rem;
  }

  .header-container {
    height: 12rem;
    color: white;
    background: -webkit-linear-gradient(to right, #ff8a00, #ff2000); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #ff8a00, #ff2000); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #ff8a00, #ff2000);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #ff8a00, #ff2000);
  }

  .header-title {
    width: 95%;
    margin: 0 auto;
    padding: 0.4rem 0;
  }

  .header-title span {
    float: left;
    font-size: 1.2rem;
  }

  .header-title a {
    float: right;
  }

  .header-title a img {
    height: 1.5rem;
    padding: 0;
  }

  .header-title a:last-child img {
    margin-right: 1.5rem;
  }

  .subtitle {
    width: 100%;
    padding: 0.4rem 0;
    overflow-x: scroll;
  }

  .subtitle ul {
    width: 40rem;
  }

  .subtitle li {
    float: left;
    padding: 0.3rem 1rem;
  }

  .subtitle .underline {
    border-bottom: 2px solid white;
  }

  .subtitle li:hover {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 3px;
  }

  .content-container {
    height: 50rem;
    margin-top: -5.6rem;
    position: relative;
  }

  .card-container {
    background-color: white;
    width: 95%;
    margin: 0 auto 0.9rem;
    padding: 0.8rem 0;
    border-radius: 1em;
    color: #3e4344;
    font-size: 0.9rem;
    text-align: left;
  }

  .card-header {
    /*background-color: yellow;*/
    position: relative;
  }

  .seller-icon {
    width: 3rem;
    border-radius: 50%;
    /*background-color: #ff8a00;*/
    margin-left: 0.8rem;
    float: left;
  }

  .icon-shop {
    width: 0.9rem;
    float: left;
    position: absolute;
    left: 2.9rem;
    top: 2rem;

  }

  .shop-name {
    padding-left: 4.4rem;
  }

  .release-time {
    padding-left: 4.4rem;
    margin-top: 0.4rem;
    color: #aba9aa;
    font-size: 0.7rem;
  }

  .delete-card {
    float: right;
    margin-right: 1.2rem;
    margin-top: -2rem;
    width: 0.7rem;
  }

  .card-content {
    /*background-color: pink;*/
    margin-top: 0.8rem;
    padding: 0 0.8rem;
  }

  .display-text {
    /*background-color: palevioletred;*/
    padding: 0.5rem 0;
    font-size: 1rem;
  }

  .display-img {
    /*background-color: palegoldenrod;*/
  }

  .display-img ul {
    /*background-color: paleturquoise;*/
  }

  .display-img li {
    float: left;
    /*background-color: red;*/
    width: calc(100% / 3);
  }

  .display-img li:nth-child(2n) {
    /*background-color: yellow;*/
  }

  .display-img img {
    width: calc(100% - 0.25rem);
    border-radius: 0.3rem;
  }

  .card-footer {
    color: #989697;
    margin: 0.45rem 1.2rem 0.25rem 0.8rem;
    font-size: 0.8rem;
  }

  .visited {
    float: left;
  }

  .evaluate {
    float: right;
  }

  .evaluate img {
    height: 1rem;
    position: relative;
    top: 0.15rem;
  }

  .follow {
    float: right;
    padding-left: 1rem;
  }

  .follow img {
    height: 1rem;
    position: relative;
    top: 0.15rem;
  }
</style>



