<template>
  <div class="sp-container">
    <div class="sp-header-container">
      <div class="sp-header-title clear-fix">
        <span></span>
        <a href="javascript:">
          <span></span>
        </a>
      </div>
      <div class="sp-subtitle clear-fix">
        <span>{{spSum}}</span>
      </div>
    </div>
    <div class="sp-card-container">
      <form action="" method="get">
        <div class="sp-card" v-for="(item, index) in cardInfo" :key="index">
          <div class="card-header">
            <label>
              <input name="shop" type="checkbox" :value="item.shopName" class="product-checkbox"/>
              <span class="checkbox-style"></span>
              <span class="display-shop-name">{{item.shopName}}<img
                src="../../static/display_shop_name_into.png"/></span>
            </label>
            <span class="get-coupon"><a href="javascript:"></a></span>
          </div>
          <div class="card-content">
            <ul>
              <li v-for="(subItem, subIndex) in item.productList" :key="subIndex" class="clear-fix">
                <label>
                  <input name="product" type="checkbox" :value="item.productName" class="product-checkbox"/>
                  <span class="checkbox-style"></span>
                </label>
                <img class="display-img" :src="subItem.productImg"/>
                <div class="display-name">{{subItem.productName}}</div>
                <div class="display-kind">
                  <span>{{subItem.productKind}}<img src="../../static/display_kind_more.png"/></span>
                </div>
                <div class="display-tip">{{subItem.tip}}</div>
                <div class="display-price-count">
                  <span class="display-price"><small></small>{{subItem.productPrice}}</span>
                  <div class="range-num-container">
                    <span @click="subProductCount(index,subIndex)" :class="{outuse:subItem.productCount===1}">
                      <a href="javascript:"></a>
                    </span>
                    <span>{{subItem.productCount}}</span>
                    <span @click="addProductCount(index,subIndex)">
                      <a href="javascript:"></a>
                    </span>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </form>
    </div>
    <div class="maybe-like-container">
      <div class="img-title"><img src="../assets/image/img-maybe-like.png"/></div>
      <div class="maybe-like-list">
        <ul class="product-card-container clear-fix">
          <li v-for="(item, index) in maybeLikeList" :key="index">
            <product-card :productImg="item.productImg"
                          :productName="item.productName"
                          :productPrice="item.productPrice"
                          :productSale="item.productSale"
                          :cardKind="2"></product-card>
          </li>
        </ul>
      </div>
    </div>
    <div class="sp-footer-container">
      <div class="sp-footer">
        <label>
          <input name="choiceAllProduct" type="checkbox" value="choiceAllProduct" class="product-checkbox"/>
          <span class="checkbox-style"></span><span></span>
        </label>
        <button class="closing-bt">(0)</button>
        <div class="display-all-price">:<span><small></small>0</span></div>
      </div>
    </div>
  </div>
</template>

<script>
  import ProductCard from "./ProductCard";

  export default {
    name: "shopping-cart",
    description: "",
    components: {ProductCard},
    data() {
      return {
        cardInfo: [
          {
            shopName: '1',
            productList: [
              {
                productName: '',
                productKind: '',
                tip: '',
                productPrice: '0.01',
                productCount: 1,
                productImg: '../static/img_product_01.png'
              },
              {
                productName: '',
                productKind: '',
                tip: '',
                productPrice: '1.00',
                productCount: 2,
                productImg: '../static/img_product_02.png'
              }
            ]
          },
          {
            shopName: '2',
            productList: [
              {
                productName: '',
                productKind: '',
                tip: '',
                productPrice: '3.00',
                productCount: 3,
                productImg: '../static/img_product_03.png'
              },
              {
                productName: '',
                productKind: '',
                tip: '',
                productPrice: '4.00',
                productCount: 4,
                productImg: '../static/img_product_04.png'
              },
              {
                productName: '',
                productKind: '',
                tip: '',
                productPrice: '5.00',
                productCount: 5,
                productImg: '../static/img_product_05.png'
              }
            ]
          }
        ],
        maybeLikeList: [
          {productName: " 1", productPrice: "111.11", productSale: "1"},
          {productName: " 2", productPrice: "222.22", productSale: "22"},
          {productName: " 3", productPrice: "333.33", productSale: "333"},
          {productName: " 4", productPrice: "444.44", productSale: "4444"}
        ]
      }
    },
    methods: {
      subProductCount(index, subIndex) {
        if (this.cardInfo[index].productList[subIndex].productCount !== 1) {
          this.cardInfo[index].productList[subIndex].productCount--;
        }
      },
      addProductCount(index, subIndex) {
        this.cardInfo[index].productList[subIndex].productCount++;
      },
    },
    computed: {
      spSum: function () {
        let result = 0;
        for (let i = 0; i < this.cardInfo.length; i++) {
          result += this.cardInfo[i].productList.length;
        }
        return result;
      },
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .sp-container {
    background-color: #f2f2f2;
    overflow-x: hidden;
    padding-bottom: 8.2rem;
  }

  .sp-header-container {
    height: 13rem;
    color: white;
    background: -webkit-linear-gradient(to right, #ff8a00, #ff2000); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #ff8a00, #ff2000); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #ff8a00, #ff2000);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #ff8a00, #ff2000);
  }

  .sp-header-title {
    width: 95%;
    margin: 0 auto;
    padding: 0.4rem 0;
  }

  .sp-header-title span {
    float: left;
    font-size: 1.2rem;
  }

  .sp-header-title a {
    float: right;
    color: white;
  }

  .sp-subtitle {
    width: 95%;
    margin: 0 auto;
    padding: 0.4rem 0;
  }

  .sp-subtitle span {
    float: left;
  }

  .sp-card-container {
    margin-top: -6.5rem;
  }

  .sp-card {
    background-color: white;
    width: 95%;
    margin: 0 auto 0.9rem;
    padding: 0.8rem 0;
    border-radius: 1em;
    color: #3e4344;
    font-size: 0.9rem;
    text-align: left;
  }

  .card-header {
    padding: 0.3rem 0.8rem 1.5rem;
  }

  .display-shop-name {
    position: relative;
    padding-left: 1.2rem;
    top: -0.5rem;
  }

  .display-shop-name img {
    width: 0.65rem;
    position: relative;
    top: 0.3rem;
    left: 0.6rem;
  }

  .get-coupon {
    display: inline-block;
    float: right;
  }

  .get-coupon a {
    color: #3e4344;
  }

  .product-checkbox {
    display: none;
  }

  .checkbox-style {
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    border: 1px solid #cccccc;
    border-radius: 50%;
    background-color: white;
  }

  .card-content li {
    position: relative;
    padding-bottom: 2.8rem;
  }

  .card-content li label {
    float: left;
    margin-top: 3.5rem;
    transform: translateY(-50%);
    padding: 0 0.8rem;
  }

  .card-content .display-img {
    width: 7rem;
    height: 7rem;
    float: left;
    padding: 0 0.8rem;
  }

  .card-content div {
    width: calc(100% - 12.8rem);
    margin-right: 0.8rem;
    float: right;
  }

  .display-kind {
    position: absolute;
    right: 0;
    bottom: 3rem;
    transform: translateY(-130%);
    color: #999999;
  }

  .display-kind span {
    display: inline-block;
    background-color: #f9f9f9;
    height: 1.7rem;
    border-radius: 0.3rem;
    line-height: 1.8rem;
    padding: 0 0.3rem;
  }

  .display-kind img {
    width: 0.4rem;
    padding-left: 0.5rem;
    padding-right: 0.3rem;
  }

  .display-tip {
    position: absolute;
    right: 0;
    bottom: 2.5rem;
    transform: translateY(-100%);
    color: #ff643c;
  }

  .display-price-count {
    position: absolute;
    right: 0;
    bottom: 1.5rem;
  }

  .display-price {
    color: #ff643c;
    font-size: 1rem;
  }

  .display-price-count .range-num-container {
    background-color: white;
    width: 6rem;
    height: 1.5rem;
    line-height: 1.6rem;
    border: 1px solid #e0e0e0;
    border-radius: 0.3rem;
  }

  .range-num-container span {
    width: 1.6rem;
    display: block;
    float: left;
    text-align: center;
  }

  .range-num-container span a {
    color: #a0a0a0;
  }

  .range-num-container span:nth-child(2) {
    width: calc(2.8rem - 2px);
    height: 100%;
    border-left: 1px solid #e0e0e0;
    border-right: 1px solid #e0e0e0;
    font-size: 0.9rem;
    color: #9a9a9a;
  }

  .range-num-container .outuse a {
    color: #e6e6e6;
  }

  .sp-footer-container {
    position: fixed;
    width: 100%;
    height: calc(4.2rem + 7.5%);
    min-height: calc(4.2rem + 43px);
    bottom: 0;
    border-top: 1px solid #e7e7e7;
    background-color: white;
  }

  .sp-footer {
    width: 100%;
    height: 4.2rem;
    line-height: 4.2rem;
    font-size: 0.9rem;
  }

  .sp-footer label {
    float: left;
    margin-left: 0.8rem;
  }

  .sp-footer label .checkbox-style {
    position: relative;
    top: 0.4rem;
  }

  .sp-footer label span:last-child {
    margin-left: 0.6rem;
  }

  .display-all-price {
    float: right;
    margin-right: 0.7rem;
  }

  .display-all-price span {
    color: #ff643c;
    font-size: 1rem;
  }

  .closing-bt {
    float: right;
    color: white;
    background: -webkit-linear-gradient(to right, #ff8a00, #ff2000); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #ff8a00, #ff2000); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #ff8a00, #ff2000);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #ff8a00, #ff2000);
    border: none;
    font-size: 1.1rem;
    border-radius: 1.4rem;
    width: 6.8rem;
    height: 3rem;
    margin-top: 0.6rem;
    margin-right: 0.8rem;
  }

  .maybe-like-container {
    height: 20rem;
  }

  .maybe-like-container .img-title img {
    width: 45%;
    padding: 1.5rem 0 0;
  }

  .product-card-container {
    font-size: 1rem;
    width: 95%;
    margin: 0 auto;
  }

  .product-card-container li {
    float: left;
    width: 50%;
  }
</style>



