<template>
  <div class="searchbar-container clear-fix">
    <div class="scan">
      <img class="des-img" src="../assets/image/searchbar-scan.png"/>
      <span class="des"></span>
    </div>
    <div class="search-container">
      <img src="../assets/image/searchbar-sicon.png"/>
      <input type="text" placeholder=""/>
      <img src="../assets/image/searchbar-tp.png"/>
    </div>
    <div class="vipcode">
      <img class="des-img" src="../assets/image/searchbar-vipcode.png"/>
      <span class="des"></span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "SearchBar"
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .searchbar-container {
    width: 100%;
    min-width: 410px;
    height: 7.5%;
    min-height: 43px;
    position: fixed;
    top: 0;
    z-index: 999;
    overflow: hidden;
    background: -webkit-linear-gradient(to right, #ff8a00, #ff2000); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #ff8a00, #ff2000); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #ff8a00, #ff2000);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #ff8a00, #ff2000);
  }

  .search-container {
    width: 72%;
    height: 100%;
    float: left;
    position: relative;
  }

  .search-container img {
    height: 70%;
    z-index: 1;
    position: absolute;
  }

  .search-container img:first-child {
    left: 1rem;
    top: 15%;
  }

  .search-container img:last-child {
    right: 1rem;
    top: 15%;
  }

  .search-container input {
    height: 70%;
    width: 70%;
    border: 0;
    border-radius: 5rem;
    padding: 0 15%;
    margin: 0;
    outline: none;
    font-size: 1rem;
    position: relative;
    top: 15%;
  }

  .scan {
    float: left;
    width: 14%;
    height: 100%;
    position: relative;
  }

  .des-img {
    height: 70%;
  }

  .des {
    font-size: 0.8rem;
    position: absolute;
    width: 100%;
    color: white;
    bottom: 0.5rem;
    left: 0;
  }

  .vipcode {
    float: right;
    width: 14%;
    height: 100%;
    position: relative;
  }
</style>



