<template>
  <div>
    <div class="login-container" v-if="isShowing === 1">
      <div class="user-icon">
        <img src="../assets/image/user-icon.png">
      </div>
      <form>
        <div class="form-item">
          <input type="text" placeholder=""/>
        </div>
        <div class="form-item">
          <input type="text" placeholder=""/>
          <a class="ignore-psw" href="javascript:"></a>
        </div>
        <button class="submit"></button>
      </form>
      <div class="choice clear-fix">
        <a href="javascript:"></a>
        <a href="javascript:" @click="register"></a>
      </div>
    </div>
    <div class="register-container" v-else-if="isShowing === 2">
      <div class="choice-nation clear-fix">
        <a><span></span><span>+86 &gt;</span></a>
      </div>
      <form>
        <div class="form-item">
          <input type="text" placeholder=""/>
        </div>
        <div class="form-item">
          <input type="text" placeholder=""/>
          <a class="get-captcha" href="javascript:"></a>
        </div>
        <button class="submit"></button>
      </form>
      <div class="agreement clear-fix">
        
        <a href="javascript:"></a>
        <a href="javascript:"></a>
        <a href="javascript:"></a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "login",
    data() {
      return {
        isShowing: 1
      };
    },
    methods: {
      register() {
        this.isShowing = 2;
        console.log('done');
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .user-icon {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #f8f8f8;
    margin: 0 auto;
  }

  .user-icon img {
    width: 100%;
  }

  .form-item {
    padding: 25px 0;
    position: relative;
  }

  .form-item input {
    width: 90%;
    height: 2em;
    border: none;
    outline: none;
    border-bottom: 1px solid #f20;
    font-size: 1.1em;
  }

  /*.form-item input::-webkit-input-placeholder { !* webkit *!*/
  /*padding-left: 2em;*/
  /*position: relative;*/
  /*!*bottom: -1em;*!*/
  /*}*/

  .ignore-psw {
    position: absolute;
    right: 5%;
    bottom: 30px;
    color: #4b4b4b;
    text-decoration: none;
    font-size: 0.9em;
  }

  .ignore-psw:hover {
    color: #232323;
  }

  .submit {
    background: linear-gradient(to right, #ff8a00, #ff2000);
    background: -webkit-linear-gradient(to right, #ff8a00, #ff2000); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #ff8a00, #ff2000); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #ff8a00, #ff2000);; /* Firefox 3.6 - 15 */
    margin-top: 2em;
    width: 90%;
    height: 50px;
    border: none;
    border-radius: 50px;
    font-size: 1.2em;
    color: white;
  }

  .choice {
    width: 90%;
    margin: 1em auto;
  }

  .choice a {
    color: #4b4b4b;
    text-decoration: none;
    font-size: 0.9em;
  }

  .choice a:hover {
    color: #232323;
  }

  .choice a:first-child {
    float: left;
  }

  .choice a:last-child {
    float: right;
  }

  .choice-nation {
    width: 90%;
    margin: 0 auto;
    padding-bottom: 1em;
  }

  .choice-nation span:first-child {
    float: left;
  }

  .choice-nation span:last-child {
    float: right;
  }

  .get-captcha {
    color: #ff8a00;
    text-decoration: none;
    font-size: 0.9em;
    position: absolute;
    right: 5%;
    bottom: 30px;
  }

  .get-captcha:hover {
    color: #ff2000;
  }

  .agreement {
    width: 90%;
    margin: 1em auto;
    font-size: 0.9em;
    text-align: left;
  }

  .agreement a {
    color: #333;
  }

  .agreement a:hover {
    color: #111;
  }

</style>



