<template>
  <div class="channel-container">
    <div class="channel-top clear-fix">
      <span class="channel-title"></span>
      <a href="javascript:">
        
        <img src="../assets/image/icon-more.png">
      </a>
      <img src="../assets/image/icon_channel_00.png">
    </div>
    <div class="channel-content">
      <ul class="clear-fix">
        <li v-for="(item, index) in channelItem" :key="index">
          <a href="javascript:"><img :src="item.url"><span v-text="item.text"></span></a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "channel",
    data() {
      return {
        channelItem: [
          {url: "../static/icon_channel_01.png", text: ""},
          {url: "../static/icon_channel_02.png", text: ""},
          {url: "../static/icon_channel_03.png", text: ""},
          {url: "../static/icon_channel_04.png", text: ""},
          {url: "../static/icon_channel_05.png", text: ""},
          {url: "../static/icon_channel_06.png", text: ""},
          {url: "../static/icon_channel_07.png", text: "88VIP"},
          {url: "../static/icon_channel_08.png", text: ""},
          {url: "../static/icon_channel_09.png", text: ""},
          {url: "../static/icon_channel_10.png", text: ""},
          {url: "../static/icon_channel_11.png", text: ""},
          {url: "../static/icon_channel_12.png", text: ""},
          {url: "../static/icon_channel_13.png", text: ""},
          {url: "../static/icon_channel_14.png", text: ""},
          {url: "../static/icon_channel_15.png", text: ""},
          {url: "../static/icon_channel_16.png", text: ""}
        ]
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .channel-container {
    width: 100%;
    font-size: 0.8rem;
  }

  .channel-top {
    width: 100%;
    position: relative;
    padding: 15px 0;
  }

  .channel-top .channel-title {
    float: left;
    position: relative;
    color: #333;
  }

  .channel-top span {
    margin-left: 10px;
  }

  .channel-top img:last-child {
    float: right;
    width: 5.6em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 10px;
    top: 50%;
  }

  .channel-top a {
    display: block;
    float: right;
    padding: 0.1rem 0.3rem;
    border-radius: 1.1rem;
    margin-right: 10px;
    color: #676767;
    text-decoration: none;
    border: 1px solid #9d9d9d;
    background-color: #fbfbfb;
    text-align: center;
  }

  .channel-top img:first-child {
    width: 0.8em;
    position: relative;
    top: 50%;
    transform: translateY(50%);
    margin-right: 0;
  }

  .channel-content {
    width: 100%;
    overflow-y: hidden;
    overflow-x: scroll;
  }

  .channel-content ul {
    width: 160%;
  }

  .channel-content a {
    color: #767676;
    display: block;
    float: left;
    padding-bottom: 10px;
    width: 12.5%;
    text-align: center;
    text-decoration: none;
  }

  .channel-content img {
    width: 60%;
  }

  .channel-content span {
    display: block;
  }
</style>



