<template>
  <div class="switch-recommend-container">
    <div class="sr-nav-container">
      <ul class="switch-recommend-nav clear-fix">
        <li v-for="(item, index) in srList" :key="index" @mouseenter="itemHover(index)" @mouseleave="itemHover(-1)">
          <a href="javascript:" @click="changeProduct(index+1)">
        <span class="sr-title" :class="{srtitlehover:changeTitleColor===index}"
              v-html="item.title">{{item.title}}</span>
            <span class="sr-subtilte" :class="{srsubtiltehover:changeTitleColor===index}">{{item.subTitle}}</span>
          </a>
        </li>
      </ul>
    </div>
    <ul v-if="choiceFlag" class="product-card-container clear-fix">
      <li v-for="(item, index) in productList" :key="index">
        <product-card :productImg="item.productImg" :productName="item.productName" :productPrice="item.productPrice"
                      :productSale="item.productSale"></product-card>
      </li>
    </ul>
  </div>
</template>

<script>
  import ProductCard from "./ProductCard";

  export default {
    name: "switch-recommend",
    components: {ProductCard},
    data() {
      return {
        srList: [
          {title: "", subTitle: ""},
          {title: "", subTitle: ""},
          {title: "", subTitle: ""},
          {title: "", subTitle: ""},
          {title: "", subTitle: ""},
          {title: "", subTitle: ""}
        ],
        changeTitleColor: -1,
        choiceFlag: 1,
        productList: []
      }
    },
    watch: {
      choiceFlag: {
        handler(newValue) {
          if (newValue === 1) {
            this.productList = [
              {productName: " 1", productPrice: "111.11", productSale: "1"},
              {productName: " 2", productPrice: "222.22", productSale: "22"},
              {productName: " 3", productPrice: "333.33", productSale: "333"},
              {productName: " 4", productPrice: "444.44", productSale: "4444"}
            ]
          }
          else if (newValue === 2) {
            this.productList = [
              {productName: "2 1", productPrice: "111.11", productSale: "1"},
              {productName: "2 2", productPrice: "222.22", productSale: "22"},
            ]
          }
          else if (newValue === 3) {
            this.productList = [
              {productName: "3 1", productPrice: "111.11", productSale: "1"},
              {productName: "3 2", productPrice: "222.22", productSale: "22"},
              {productName: "3 3", productPrice: "333.33", productSale: "333"}
            ]
          }
          else if (newValue === 4) {
            this.productList = [
              {productName: "4 1", productPrice: "111.11", productSale: "1"},
              {productName: "4 2", productPrice: "222.22", productSale: "22"},
            ]
          }
          else if (newValue === 5) {
            this.productList = [
              {productName: "5 1", productPrice: "111.11", productSale: "1"},
              {productName: "5 2", productPrice: "222.22", productSale: "22"},
            ]
          }
          else if (newValue === 6) {
            this.productList = [
              {productName: "6 1", productPrice: "111.11", productSale: "1"},
              {productName: "6 2", productPrice: "222.22", productSale: "22"},
            ]
          }
        },
        immediate: true
      }
    },
    methods: {
      itemHover(index) {
        this.changeTitleColor = index;
      },
      changeProduct(index) {
        this.choiceFlag = index;
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .switch-recommend-container {
    width: 100%;
    min-width: 410px;
    font-size: 1rem;
    margin: 10px;
    text-align: center;
    position: relative;
  }

  .sr-nav-container {
    width: 100%;
    overflow-y: hidden;
    overflow-x: scroll;
  }

  .switch-recommend-nav {
    width: 120%;
  }

  .switch-recommend-nav li {
    float: left;
    border-left: 1px solid #cecfce;
    width: 13%;
    margin-left: 10px;
    padding-left: 10px;
  }

  .switch-recommend-nav li:first-child {
    padding: 0;
    margin: 0;
  }

  .sr-title {
    display: block;
    color: #313031;
    font-size: 0.9rem;
  }

  .sr-subtilte {
    display: block;
    color: #9c9a9c;
    font-size: 0.8rem;
  }

  .srtitlehover {
    color: #ff2000;
  }

  .srsubtiltehover {
    color: white;
    background: -webkit-linear-gradient(to right, #ff8a00, #ff2000); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #ff8a00, #ff2000); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #ff8a00, #ff2000);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #ff8a00, #ff2000);
    border-radius: 1rem;
  }

  .product-card-container {
    font-size: 1rem;
    width: 95%;
    margin: 0 auto;
    padding-left: 10px;
    margin-left: -10px;
  }

  .product-card-container li {
    float: left;
    width: 50%;
  }
</style>



