<template>
  <div class="scroll-box" :style="{marginTop:boxMTop}">
    <ul class="scroll-list" :class="{animation:flag}">
      <li v-for="(item, index) in scorllImgList" :key="index">
        <img :src="item.url">
      </li>
    </ul>
    <ul class="scroll-bt">
      <li :class="{lstyle:lflag[0]}"></li>
      <li :class="{lstyle:lflag[1]}"></li>
      <li :class="{lstyle:lflag[2]}"></li>
      <li :class="{lstyle:lflag[3]}"></li>
      <li :class="{lstyle:lflag[4]}"></li>
      <li :class="{lstyle:lflag[5]}"></li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "scroll-box",
    description: "",
    data() {
      return {
        flag: false,
        scorllImgList: [
          {
            url: "../static/img_scroll_1.png"
          },
          {
            url: "../static/img_scroll_2.png"
          },
          {
            url: "../static/img_scroll_3.png"
          },
          {
            url: "../static/img_scroll_4.png"
          },
          {
            url: "../static/img_scroll_5.png"
          },
          {
            url: "../static/img_scroll_6.png"
          }
        ],
        lflag: [true, false, false, false, false, false],
        lindex: 1,
        boxMTop: (window.innerHeight * 0.075) + 'px'
      }
    },
    created: function () {
      setInterval(this.showBox, 1000);
    },
    watch: {
      lindex: function (newValue) {
        if (newValue === 1) {
          this.lflag = [true, false, false, false, false, false];
        }
        else if (newValue === 2) {
          this.lflag = [false, true, false, false, false, false];
        }
        else if (newValue === 3) {
          this.lflag = [false, false, true, false, false, false];
        }
        else if (newValue === 4) {
          this.lflag = [false, false, false, true, false, false];
        }
        else if (newValue === 5) {
          this.lflag = [false, false, false, false, true, false];
        }
        else if (newValue === 6) {
          this.lflag = [false, false, false, false, false, true];
        }
      }
    },
    methods: {
      mounted() {
        window.onresize = function () {
          this.boxHeight = (window.innerWidth * 0.31) + 'px';
        }
      },
      showBox: function () {
        this.flag = true;
        this.lindex++;
        if (this.lindex === this.lflag.length + 1) this.lindex = 1;
        setTimeout(() => {
          this.scorllImgList.push(this.scorllImgList[0]);
          this.scorllImgList.shift();
          this.flag = false;
        }, 800);
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .scroll-box {
    width: 100%;
    height: 0;
    padding-top: 31.2%;
    overflow: hidden;
    position: relative;
  }

  .scroll-box:after {
    content: "";
    display: block;
    background: -webkit-linear-gradient(to right, #fcfcfc, #f1f1f1); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #fcfcfc, #f1f1f1); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #fcfcfc, #f1f1f1);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #fcfcfc, #f1f1f1);
    width: 100%;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    bottom: -10px;
  }

  .scroll-list {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .scroll-list img {
    width: 200%;
  }

  .scroll-bt {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
  }

  .scroll-bt li {
    display: block;
    width: 0.5rem;
    height: 0.5rem;
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    background-color: rgba(225, 225, 225, 0.5);
    float: left;
    margin: 0.3rem;
  }

  .scroll-bt .lstyle {
    background-color: rgba(225, 225, 225, 0.9);
  }

  .animation {
    transition: all 0.8s;
    margin-left: -100%;
  }
</style>



