<template>
  <div class="home-container">
    <search-bar></search-bar>
    <scroll-box></scroll-box>
    <channel></channel>
    <tb-news></tb-news>
    <switch-recommend></switch-recommend>
  </div>
</template>

<script>
  import SearchBar from "./SearchBar";
  import ScrollBox from "./ScrollBox";
  import Channel from "./Channel";
  import TbNews from "./TbNews";
  import SwitchRecommend from "./SwitchRecommend";

  export default {
    name: "home",
    description: "",
    components: {
      SearchBar,
      ScrollBox,
      Channel,
      TbNews,
      SwitchRecommend
    },
    data() {
      return {}
    },
    methods: {},
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .home-container {
    background: -webkit-linear-gradient(to right, #fcfcfc, #f1f1f1); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(to right, #fcfcfc, #f1f1f1); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(to right, #fcfcfc, #f1f1f1);; /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #fcfcfc, #f1f1f1);
    overflow-x: hidden;
    padding-bottom: 4rem;
  }
</style>



