import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import home from '@/components/homePage'
import tinyTb from '@/components/TinyTbPage'
import news from '@/components/NewsPage'
import shoppingCart from '@/components/ShoppingCartPage'
import myTb from '@/components/MyTbPage'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/tinyTb',
      name: 'tinyTb',
      component: tinyTb
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/shoppingCart',
      name: 'shoppingCart',
      component: shoppingCart
    },
    {
      path: '/myTb',
      name: 'myTb',
      component: myTb
    }
  ]
})



