<template>
  <div class="nav-footer-container">
    <ul class="clear-fix">
      <li @click="changeButton(1)">
        <a href="javascript:">
          <img :src="choice===1?'../static/nav_footer_choice_1.png':'../static/nav_footer_1.png'"/>
        </a>
      </li>
      <li @click="changeButton(2)">
        <a href="javascript:">
          <img :src="choice===2?'../static/nav_footer_choice_2.png':'../static/nav_footer_2.png'"/>
        </a>
      </li>
      <li @click="changeButton(3)">
        <a href="javascript:">
          <img :src="choice===3?'../static/nav_footer_choice_3.png':'../static/nav_footer_3.png'"/>
        </a>
      </li>
      <li @click="changeButton(4)">
        <a href="javascript:">
          <img :src="choice===4?'../static/nav_footer_choice_4.png':'../static/nav_footer_4.png'"/>
        </a>
      </li>
      <li @click="changeButton(5)">
        <a href="javascript:">
          <img :src="choice===5?'../static/nav_footer_choice_5.png':'../static/nav_footer_5.png'"/>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "nav-footer",
    description: "",
    data() {
      return {
        choice: 1,
        pageName: ['home', 'tinyTb', 'news', 'shoppingCart', 'myTb']
      }
    },
    methods: {
      changeButton(num) {
        this.choice = num;//choice
        this.$router.push(this.pageName[num - 1]);//
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .nav-footer-container {
    width: 100%;
    min-width: 410px;
    height: 7.5%;
    min-height: 43px;
    background-color: white;
    border-top: 1px solid #e4e4e4;
    position: fixed;
    z-index: 999;
    bottom: 0;
    left: 0;
  }

  .nav-footer-container ul {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
  }

  .nav-footer-container li {
    width: 20%;
    height: 100%;
    float: left;
  }

  .nav-footer-container li a {
    display: inline-block;
    height: 100%;
  }

  .nav-footer-container li img {
    width: auto;
    height: 100%;
  }
</style>



