<template>
  <div class="tbnews-container clear-fix">
    <div class="tbnews-header">
      <img src="../assets/image/icon_sales_promotion_01.png">
      <div>
        <span></span>
        <span></span>
      </div>
    </div>
    <div class="tbnews-content clear-fix" v-for="(item, index) in displayInfo" :key="index">
      <div class="display-l clear-fix">
        <div class="display-top">
          <img :src="item.dTopUrlL"/>
          <div>
            <!--<span></span>:<span></span>:<span></span>-->
          </div>
        </div>
        <div class="display-left">
          <span>{{item.textL}}</span>
          <img :src="item.dLeftUrlL"/>
        </div>
        <div class="display-right">
          <img :src="item.dRightUrlL"/>
        </div>
      </div>
      <div class="display-r clear-fix">
        <div class="display-top">
          <img :src="item.dTopUrlR"/>
        </div>
        <div class="display-left">
          <span>{{item.textR}}</span>
          <img :src="item.dLeftUrlR"/>
        </div>
        <div class="display-right">
          <img :src="item.dRightUrlR"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "tb-news",
    data() {
      return {
        displayInfo: [
          {
            dTopUrlL: "../static/tb_news_top_01.png",
            dTopTime: [11, 22, 10],
            textL: "1",
            dLeftUrlL: "../static/tb_news_left_01.png",
            dRightUrlL: "../static/tb_news_right_01.png",
            dTopUrlR: "../static/tb_news_top_02.png",
            textR: "2",
            dLeftUrlR: "../static/tb_news_left_02.png",
            dRightUrlR: "../static/tb_news_right_02.png"
          },
          {
            dTopUrlL: "../static/tb_news_top_03.png",
            textL: "3",
            dLeftUrlL: "../static/tb_news_left_03.png",
            dRightUrlL: "../static/tb_news_right_03.png",
            dTopUrlR: "../static/tb_news_top_04.png",
            textR: "4",
            dLeftUrlR: "../static/tb_news_left_04.png",
            dRightUrlR: "../static/tb_news_right_04.png"
          },
          {
            dTopUrlL: "../static/tb_news_top_05.png",
            textL: "5",
            dLeftUrlL: "../static/tb_news_left_05.png",
            dRightUrlL: "../static/tb_news_right_05.png",
            dTopUrlR: "../static/tb_news_top_06.png",
            textR: "6",
            dLeftUrlR: "../static/tb_news_left_06.png",
            dRightUrlR: "../static/tb_news_right_06.png"
          }
        ]
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/css.css";

  .tbnews-container {
    font-size: 1rem;
    width: 95%;
    margin: 10px auto;
    border-radius: 1em;
    background-color: white;
    border-collapse: collapse;
    overflow: hidden;
  }

  .tbnews-header {
    width: 100%;
    height: 3rem;
    display: flex;
    align-items: center;
  }

  .tbnews-header img {
    margin-left: 15px;
    height: 33%;
  }

  .tbnews-header span {
    margin-left: 15px;
    font-size: 0.9rem;
    color: #333333;
  }

  .tbnews-header span:first-child {
    background-color: #ffeded;
    color: #ff2300;
    border-radius: 6px;
    padding: 0.1rem 0.4rem;
  }

  .display-l {
    width: 49%;
    height: 9rem;
    border-top: 1px solid #f3f3f3;
    border-right: 1px solid #f3f3f3;
    position: relative;
    overflow: hidden;
    float: left;
  }

  .display-r {
    width: 50%;
    height: 9rem;
    border-top: 1px solid #f3f3f3;
    overflow: hidden;
    float: left;
    position: relative;
  }

  .display-l div {
    float: left;
  }

  .display-top {
    width: 100%;
    height: 25%;
    line-height: 2rem;
  }

  .display-top img {
    height: 100%;
    float: left;
  }

  .display-top div {
    float: left;
    margin-left: 0.1rem;
    transform: translateY(20%);
    color: #ff003c;
  }

  .display-top div span {
    background-color: #ff003c;
    font-size: 0.6rem;
    padding: 0 0.1rem;
    border-radius: 0.2rem;
    color: white;
  }

  .display-left {
    width: 50%;
    height: 75%;
    position: relative;
    z-index: 1;
    float: left;
  }

  .display-left span {
    display: block;
    width: 160%;
    text-align: left;
    height: 1.5rem;
    font-size: 0.7rem;
    color: #9a9a9a;
    padding: 0 0.7rem;
  }

  .display-left img {
    width: 100%;
  }

  .display-right {
    width: 50%;
    height: 100%;
    float: left;
    position: relative;
  }

  .display-right img {
    width: 100%;
    position: absolute;
    right: 0;
  }
</style>



